import React from 'react';
import logo from './logo.svg';
import './App.css';

function App() {

  const names: { name: string, age: number }[] = [{ name: "joe", age: 4 }, { name: "ryan", age: 47 }];
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.tsx</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
      <h1>The Peeps!</h1>
      {
        names.map((e) => <>
          <h2>Name: {e.name}</h2>
          <h6>Age: {e.age}</h6>
        </>
        )
      }
    </div>
  );
}

export default App;
